import service_user from '@/http/services/user.js'
export default {
  name: "Publicity",
  data(){
      return{
        share: false,
        qrcode: '',
        token: '',
      }
  },
  methods:{
    showShare() {
      this.share = true;
    },
    changeShare(msg) {
      this.share = msg;
    },
    getCookie: function () {
        this.token = this.$cookies.get('access_token');
    },
    getQrcode: function () {
      service_user.userService.getQrcode(this.token).then(res => {
        if (res.status === 200) {
          this.qrcode = res.data;
        }
      })
    },
  },
 mounted() {
   this.getCookie();
   this.getQrcode();
 },
}



