import service from '@/http/services/book.js'
  export default {
      name: "Library",
    data() {
        return {
          books:'',
          loading: true
        }
    },

    methods: {
      initbooklist: function () {
        service.bookService.list().then(res => {
          if (res.status === 200) {
            this.books=res.data;
            this.loading = false;
          }
        })
      },
    },
    mounted:function(){
      this.initbooklist();
    }
  }





