
import service_user from '@/http/services/user.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: "MyEarnings",
data(){
return{
value1: '2015-11-12',
value2: '2015-11-12',
access_token: '',
income : '',
settlement: '',
monthincome:'',
}
},
components: {
Datetime,
Group
},
methods:{
log (str1, str2 = '') {
console.log(str1, str2)
},
onConfirm (val) {
console.log('on-confirm arg', val)
console.log('current value', this.value1)
},
change (value) {
console.log('change', value)
},
log2 (str1, str2 = '') {
console.log(str1, str2)
},
onConfirm2 (val) {
console.log('on-confirm arg', val)
console.log('current value', this.value2)
},
change2 (value) {
console.log('伍浩伟 ', value)
service_user.userService.incomeMonthCheck({'access-token': this.access_token,'date' :this.value1,'date2' : this.value2}).then(res => {
if (res.status === 200) {
this.monthincome = res.data;
console.log(this.monthincome)
}
})
},
init() {
service_user.userService.income({'access-token': this.access_token}).then(res => {
if (res.status === 200) {
this.income = res.data.income;
this.settlement = res.data.settlement;
console.log(this.income.income)

}
})
},
initMonthCheck() {
service_user.userService.incomeMonth({'access-token': this.access_token}).then(res => {
if (res.status === 200) {
this.monthincome = res.data;
console.log(this.monthincome)
}
})
},
},
mounted() {
this.access_token = this.$cookies.get("access_token");
this.init();
this.initMonthCheck();
}
}



