export default {
  name: "QualityCourseVideo",
  data(){
    return{
      id: 0,
      share: false,
      video_url:'',
      title: '',
      pic: ''
    }
  },
  created() {
    this.video_url = this.$route.params.video_url;
    this.title = this.$route.params.title;
    this.pic = this.$route.params.pic;
    console.log(this.pic);
  },
  methods:{
    changeShare(msg){
      this.share = msg;
    },
    showShare(){
      this.share = true;
    }
  }
}



