
import service_user from '@/http/services/user.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: "MyGeneralize",
data(){
return{
value1: '请选择时间',
incomes : '',
show : true,
access_token :'',
}
},
components: {
Datetime,
Group
},
methods:{

change (value) {
this.show=false,
console.log('change', value)
console.log("change!!")
service_user.userService.incomeCheck({'date': this.value1, 'access-token': this.access_token}).then(res => {
if (res.status === 200) {
console.log(res.data);
this.incomes = res.data;
if(!(JSON.stringify(res.data)==0)) {
this.show = true;
}
}
})
},

onConfirm (val) {
console.log('on-confirm arg', val)
console.log('current value', this.value1)
},

log (str1, str2 = '') {
console.log(str1, str2)
},
init() {
service_user.userService.incomeStatistics({'date': '2015-03-11', 'access-token': this.access_token}).then(res => {
if (res.status === 200) {
console.log(res.data);
this.incomes = res.data;
}
})
}
},
mounted() {
this.access_token = this.$cookies.get('access_token');
this.init();
}
}



