import service from '@/http/services/course.js'
export default {
  name: "ClassQuality",
  data() {
    return {
      courseList: '',
      loading: true
    }
  },

  methods: {
    getCourseList: function () {
      service.courseService.courseList({}).then(res => {
        if (res.status === 200) {
          this.courseList = res.data;
          this.loading = false;
        }
      })
    }
  },

  mounted: function () {
    this.getCourseList();
  }
}



