import  service from '@/http/services/package.js'
export default {
  name: "set_meal",
  data() {
     return {
       packageList: '',
       loading: true
     }
  },

  methods: {

    getList() {
      service.packageService.packageList().then(res => {
        if (res.status === 200 && res.data.status == 0) {
          this.packageList = res.data.packages;
          this.loading = false;
        } else {
          alert('wrong!');
        }
      })
    }

  },

  mounted: function () {
    this.getList();
  }
}



