// require styles
import 'swiper/dist/css/swiper.css'
import service from '@/http/services'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

  export default {
    name: "home",
    components:{
        swiper,
        swiperSlide
    },
    data() {
      return {
        swiperOption: {
          // some swiper options/callbacks
          // 所有的参数同 swiper 官方 api 参数
          // ...
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
          autoplay: {
            delay: 3000,
            stopOnLastSlide: false,
            disableOnInteraction: false,
          },
          loop : true,
        },
        swiperNotice:{
          autoplay: {
            delay: 2000,
            stopOnLastSlide: false,
            disableOnInteraction: false,
          },
          loop : true,
          direction : 'vertical',
        },
        token: this.$cookies.get('access_token'),
        // 是否显示最近在学
        recentLearningShow: false,
        // 幻灯片
        ads: [],
        // 公告
        notices: [],
        // 资讯
        information: [],
        // vip套餐
        packages: [],
        // 精品课程
        courses: [],
        // 公开课
        opens: [],
        // 推荐图书
        books: [],
        // 最近在学
        recentStudy: {},
        // 加载动画
        loading: true
      }
    },
    methods: {
      getHomeData() {
        service.homeService.getHomeData(this.token).then(res => {
          if (res.status === 200 && res.data.status === 0) {
            this.ads = res.data.data.ads;
            this.notices = res.data.data.notices;
            this.information = res.data.data.information;
            this.packages = res.data.data.packages;
            this.courses = res.data.data.courses;
            this.opens = res.data.data.opens;
            this.books = res.data.data.books;
            this.loading = false;
            if(this.token && this.recent) {
              this.recentStudy = this.recent
            }
          }
        })
      },
      gotoList(tag) {
        switch(tag) {
          case 'information':
            this.$router.push('informationList');
            break;
          case 'setMeal':
            this.$router.push('SetMeal');
            break;
          case 'exCourse':
            this.$router.push('ClassQuality');
            break;
          case 'recommdBooks':
            this.$router.push('Library');
            break;
        }
      }
    },
    mounted () {
      this.getHomeData();
    },
    computed: {
      swiper () {
        return this.$refs.mySwiper.swiper
      }
    }
  }



