import service from '@/http/services/information.js';
export default {
  name: "InformationList",
  data () {
    return {
      information: [],
      loading: true
    }
  },
  methods:{
    initinformation: function () {
      service.informationService.list().then(res => {
        if (res.status === 200) {
          this.information = res.data;
          this.loading = false;
        }
      })
    },
  },
  mounted() {
    this.initinformation();
  }
}



