import service_user from '@/http/services/user.js'
export default {
    name: "Message",
    data() {
      return {
        messages: '',
        token : '',
        loading: true
      }
    },
    methods: {
      getCookie :function () {
        this.token = this.$cookies.get('access_token');
      },
      getMessage :function () {
        service_user.userService.getMessage(this.token).then(res => {
        if (res.status === 200) {
          this.messages = res.data;
          this.loading = false;
          console.log(this.messages);
        }
      })
      }
    },
    mounted:function () {
      this.getCookie();
      this.getMessage();
  }
}



